___CSS_LOADER_EXPORT___.push([module.id, `html,
body {
  height: 100%;
  width: 100%;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
}

#image-carousel {
  position: relative;
  height: 50%;
  width: 50%;
  display: grid;
  grid-template-columns: 0.5fr 5fr 0.5fr;
  grid-template-rows: 10fr 1fr;
}

#back-button {
  grid-column: 1/2;
  grid-row: 1/3;
}

#image-container {
  grid-column: 2/3;
  grid-row: 1/2;
  display: grid;
  place-items: center;
  overflow: hidden;
}

#indicator-container {
  grid-column: 2/3;
  grid-row: 2/3;
  display: flex;
  justify-content: center;
  gap: 10px;
}

#forwards-button {
  grid-column: 3/4;
  grid-row: 1/3;
}

.arrow-image {
  height: auto;
  width: 100%;
}

#back-button,
#forwards-button {
  border-radius: 10px;
  background-color: transparent;
  border: none;
  outline: none;
}

#back-button:hover,
#forwards-button:hover {
  transition: background-color 0.5s ease;
  background-color: rgb(216, 216, 216);
}

.active-img {
  max-height: 100%;
  max-width: 100%;
  display: block;
  height: auto;
  width: auto;
}
